/* Basic */
body {
  background: #f7f7f7 url("images/bg01.png");
}
body.is-loading * {
  transition: none !important;
  animation: none !important;
}

body,
input,
select,
textarea {
  color: #474747;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16pt;
  font-weight: 300;
  line-height: 1.65em;
}

a {
  transition: color 0.2s ease-in-out, border-color 0.2s ease-in-out, opacity 0.2s ease-in-out;
  color: #75b04b;
  text-decoration: none;
  border-bottom: dotted 1px;
}
a.no-b {
  border-bottom: none;
}
a svg {
  transition: fill 0.2s ease;
}
a:hover {
  color: #75b04b;
  border-bottom-color: transparent;
}
a:hover svg {
  fill: #75b04b;
}

strong,
b {
  font-weight: 600;
}

em,
i {
  font-style: italic;
}

p,
ul,
ol,
dl,
table,
blockquote {
  margin: 0 0 2em 0;
}

h1, #header .title,
h2,
#main_header,
h3,
h4,
h5,
h6 {
  color: inherit;
  font-weight: 600;
  line-height: 1.75em;
  margin-bottom: 1em;
}
h1 a, #header .title a,
h2 a,
#main_header a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
  text-decoration: none;
}
h1 em, #header .title em,
h2 em,
#main_header em,
h3 em,
h4 em,
h5 em,
h6 em {
  font-style: normal;
  font-weight: 300;
}

h2, #main_header {
  font-size: 1.75em;
  letter-spacing: -0.025em;
}

h3 {
  font-size: 1.2em;
  letter-spacing: -0.025em;
}

sub {
  font-size: 0.8em;
  position: relative;
  top: 0.5em;
}

sup {
  font-size: 0.8em;
  position: relative;
  top: -0.5em;
}

hr {
  border-top: solid 1px #e0e0e0;
  border: 0;
  margin-bottom: 1.5em;
}

blockquote {
  border-left: solid 0.5em #e0e0e0;
  font-style: italic;
  padding: 1em 0 1em 2em;
}

section.special,
article.special {
  text-align: center;
}

header p {
  color: #999;
  font-size: 1.25em;
  position: relative;
  margin-top: -1.25em;
  margin-bottom: 2.25em;
}
header.major {
  text-align: center;
  margin: 0 0 2em 0;
}
header.major h2, header.major #main_header {
  font-size: 2.25em;
}
header.major p {
  position: relative;
  border-top: solid 1px #e0e0e0;
  padding: 1em 0 0 0;
  margin: 0;
  top: -1em;
  font-size: 1.5em;
  letter-spacing: -0.025em;
}

footer {
  margin: 0 0 3em 0;
}
footer > :last-child {
  margin-bottom: 0;
}
footer.major {
  padding-top: 3em;
}

/* Form */
input[type=text],
input[type=password],
input[type=email],
textarea {
  appearance: none;
  transition: border-color 0.2s ease-in-out;
  background: #fff;
  border: solid 1px #e0e0e0;
  border-radius: 5px;
  color: inherit;
  display: block;
  outline: 0;
  padding: 0.75em;
  text-decoration: none;
  width: 100%;
}
input[type=text]:focus,
input[type=password]:focus,
input[type=email]:focus,
textarea:focus {
  border-color: #75b04b;
}

input[type=text],
input[type=password],
input[type=email] {
  line-height: 1em;
}

label {
  display: block;
  color: inherit;
  font-weight: 600;
  line-height: 1.75em;
  margin-bottom: 0.5em;
}

::-webkit-input-placeholder {
  color: #999;
  position: relative;
  top: 3px;
}

:-moz-placeholder {
  color: #999;
}

::-moz-placeholder {
  color: #999;
}

:-ms-input-placeholder {
  color: #999;
}

.formerize-placeholder {
  color: #999;
}

/* Image */
.image {
  border: 0;
  display: inline-block;
  position: relative;
  border-radius: 5px;
}
.image img {
  display: block;
  border-radius: 5px;
}
.image.left {
  display: block;
  float: left;
  margin: 0 2em 2em 0;
  position: relative;
  top: 0.25em;
}
.image.left img {
  display: block;
  width: 100%;
}
.image.fit {
  display: block;
}
.image.fit img {
  display: block;
  width: 100%;
}
.image.featured {
  display: block;
  margin: 0 0 2em 0;
}
.image.featured img {
  display: block;
  width: 100%;
}

/* Icon */
.icon {
  text-decoration: none;
  position: relative;
  text-decoration: none;
}
.icon:before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  text-transform: none !important;
}
.icon > .label {
  display: none;
}
.icon.major {
  text-align: center;
  cursor: default;
  background-color: #75b04b;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 80%;
  color: #fff;
  border-radius: 100%;
  display: inline-block;
  width: 5em;
  height: 5em;
  line-height: 5em;
  box-shadow: 0 0 0 7px white, 0 0 0 8px #e0e0e0;
  margin: 0 0 2em 0;
}
.icon.major:before {
  font-size: 36px;
}

/* Lists */
ol.default {
  list-style: decimal;
  padding-left: 1.25em;
}
ol.default li {
  padding-left: 0.25em;
}

ul.default {
  list-style: disc;
  padding-left: 1em;
}
ul.default li {
  padding-left: 0.5em;
}
ul.links li {
  line-height: 2.5em;
}
ul.icons {
  cursor: default;
}
ul.icons li {
  display: inline-block;
  line-height: 1em;
  padding-left: 1.5em;
}
ul.icons li:first-child {
  padding-left: 0;
}
ul.icons li a,
ul.icons li span {
  font-size: 2em;
  border: 0;
}
ul.actions {
  cursor: default;
}
ul.actions:last-child {
  margin-bottom: 0;
}
ul.actions li {
  display: inline-block;
  padding: 0 0 0 1.5em;
}
ul.actions li:first-child {
  padding: 0;
}
ul.actions.vertical li {
  display: block;
  padding: 1.5em 0 0 0;
}
ul.actions.vertical li:first-child {
  padding: 0;
}
ul.menu li {
  border-left: solid 1px #e0e0e0;
  display: inline-block;
  padding: 0 0 0 1em;
  margin: 0 0 0 1em;
}
ul.menu li:first-child {
  border-left: 0;
  margin-left: 0;
  padding-left: 0;
}

/* Tables */
table {
  width: 100%;
}
table.default {
  width: 100%;
}
table.default tbody tr {
  border-bottom: solid 1px #e0e0e0;
}
table.default td {
  padding: 0.5em 1em 0.5em 1em;
}
table.default th {
  font-weight: 600;
  padding: 0.5em 1em 0.5em 1em;
  text-align: left;
}
table.default thead {
  background-color: #555555;
  background-image: linear-gradient(top, rgba(0,0,0,0), rgba(0,0,0,0.15)), url("images/bg01.png");
  color: #fff;
}
/* Button */
input[type=submit],
input[type=reset],
input[type=button],
.button {
  appearance: none;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  background-image: linear-gradient(top, rgba(0,0,0,0), rgba(0,0,0,0.15)), url("images/bg01.png");
  background-color: #75b04b;
  border-radius: 5px;
  border: 0;
  color: #fff !important;
  cursor: pointer;
  display: inline-block;
  padding: 0 1.5em;
  line-height: 2.75em;
  min-width: 9em;
  text-align: center;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: -0.025em;
}
input[type=submit].view-all,
input[type=reset].view-all,
input[type=button].view-all,
.button.view-all {
  display: block;
}
input[type=submit]:hover,
input[type=reset]:hover,
input[type=button]:hover,
.button:hover {
  background-color: #82b95b;
  color: #fff !important;
}
input[type=submit]:active,
input[type=reset]:active,
input[type=button]:active,
.button:active {
  background-color: #699e43;
  color: #fff;
}
input[type=submit].alt,
input[type=reset].alt,
input[type=button].alt,
.button.alt {
  background-color: #555555;
  color: #fff;
}
input[type=submit].alt:hover,
input[type=reset].alt:hover,
input[type=button].alt:hover,
.button.alt:hover {
  background-color: #626262;
}
input[type=submit].alt:active,
input[type=reset].alt:active,
input[type=button].alt:active,
.button.alt:active {
  background-color: #484848;
}
input[type=submit].icon:before,
input[type=reset].icon:before,
input[type=button].icon:before,
.button.icon:before {
  margin-right: 0.5em;
}
input[type=submit].fit,
input[type=reset].fit,
input[type=button].fit,
.button.fit {
  width: 100%;
}
input[type=submit].small,
input[type=reset].small,
input[type=button].small,
.button.small {
  font-size: 0.8em;
}

/* Box */
.box.highlight {
  text-align: center;
}
.box.post {
  position: relative;
  margin: 0 0 2em 0;
}
.box.post:after {
  content: "";
  display: block;
  clear: both;
}
.box.post .inner {
  margin-left: calc(30% + 2em);
}
.box.post .inner > :last-child {
  margin-bottom: 0;
}
.box.post .inner h3 {
  line-height: 1em;
}
.box.post .inner h3 a {
  border: none;
}
.box.post .inner h3 a:hover {
  color: #75b04b;
}
.box.post .inner .read-more {
  color: #75b04b;
  font-size: 0.8em;
  float: right;
  line-height: 1.5em;
  padding-top: 0.5em;
}
.box.post .image {
  width: 30%;
  margin: 0;
}

/* Header */
#header {
  text-align: center;
  padding: 3em 0 0 0;
  background-color: #fff;
  background-image: inline-image("images/bg02.png"), inline-image("images/bg02.png"), inline-image("images/bg01.png");
  background-position: top left, top left, top left;
  background-size: 100% 6em, 100% 6em, auto;
  background-repeat: no-repeat, no-repeat, repeat;
}
#header .title {
  padding: 0 0 2.75em 0;
  margin: 0;
}
#header .title a {
  font-size: 2em;
  font-family: "IM Fell DW Pica SC", serif;
  letter-spacing: 0.025em;
  font-weight: 300;
  border: 0;
}

#nav {
  cursor: default;
  background-color: #333;
  background-image: linear-gradient(top, rgba(0,0,0,0), rgba(0,0,0,0.3)), url("images/bg01.png");
  padding: 0;
}
#nav:after {
  content: "";
  display: block;
  width: 100%;
  height: 0.75em;
  background-color: #75b04b;
  background-image: url("images/bg01.png");
}
#nav > ul {
  margin: 0;
}
#nav > ul > li {
  position: relative;
  display: inline-block;
  margin-left: 1em;
}
#nav > ul > li a {
  color: #c0c0c0;
  text-decoration: none;
  border: 0;
  display: block;
  padding: 1.5em 0.5em 1.35em 0.5em;
}
#nav > ul > li:first-child {
  margin-left: 0;
}
#nav > ul > li:hover a {
  color: #fff;
}
#nav > ul > li.current {
  font-weight: 600;
}
#nav > ul > li.current:before {
  transform: rotateZ(45deg);
  width: 0.75em;
  height: 0.75em;
  content: "";
  display: block;
  position: absolute;
  bottom: -0.5em;
  left: 50%;
  margin-left: -0.375em;
  background-color: #75b04b;
  background-image: url("images/bg01.png");
}
#nav > ul > li.current a {
  color: #fff;
}
#nav > ul > li.active a {
  color: #fff;
}
#nav > ul > li.active.current:before {
  opacity: 0;
}
#nav > ul > li > ul {
  display: none;
}

/* Dropotron */
.dropotron {
  background-color: #333;
  background-image: linear-gradient(top, rgba(0,0,0,0.3), rgba(0,0,0,0)), url("images/bg01.png");
  border-radius: 5px;
  color: #fff;
  min-width: 10em;
  padding: 1em 0;
  text-align: center;
  box-shadow: 0 1em 1em 0 rgba(0, 0, 0, 0.5);
}
.dropotron > li {
  line-height: 2em;
  padding: 0 1.1em 0 1em;
}
.dropotron > li > a {
  color: #c0c0c0;
  text-decoration: none;
  border: 0;
}
.dropotron > li.active > a, .dropotron > li:hover > a {
  color: #fff;
}
.dropotron.level-0 {
  border-radius: 0 0 5px 5px;
  font-size: 0.9em;
  padding-top: 0;
  margin-top: -1px;
}

/* Banner */
.banner {
  background-position: center center;
  background-size: cover;
  box-shadow: inset 0 0 10vw black;
  height: 28em;
  text-align: center;
  position: relative;
}
.banner header {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #212121;
  background: rgba(27, 27, 27, 0.75);
  color: #fff;
  padding: 1.5em 0;
}
.banner header h2, .banner header #main_header {
  display: inline-block;
  margin: 0;
  font-size: 1.25em;
  vertical-align: middle;
}
.banner header h2 em, .banner header #main_header em {
  opacity: 0.75;
}
.banner header h2 a, .banner header #main_header a {
  border-bottom-color: rgba(255, 255, 255, 0.5);
}
.banner header h2 a:hover, .banner header #main_header a:hover {
  border-bottom-color: transparent;
}
.banner header .button {
  vertical-align: middle;
  margin-left: 1em;
}

/* Wrapper */
.wrapper {
  padding: 5em 0 3em 0;
}
.wrapper.style1 {
  background: #fff;
}
.wrapper.style1 a {
  color: #474747;
}
.wrapper.style2 {
  background-color: #fff;
  background-image: inline-image("images/bg02.png"), inline-image("images/bg03.png"), inline-image("images/bg01.png");
  background-position: top left, bottom left, top left;
  background-size: 100% 6em, 100% 6em, auto;
  background-repeat: no-repeat, no-repeat, repeat;
}
.wrapper.style3 {
  background-color: #75b04b;
  background-image: linear-gradient(top, rgba(0,0,0,0), rgba(0,0,0,0.15)), url("images/bg01.png");
  color: #fff;
}
.wrapper.style3 .button {
  background: #fff;
  color: #474747;
}
.wrapper.style3 .button:hover {
  color: #75b04b !important;
}

/* CTA */
#cta {
  text-align: center;
  padding: 3.5em 0;
}
#cta header h2, #cta header #main_header {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
}
#cta header .button {
  vertical-align: middle;
  margin-left: 1em;
}

/* Footer */
#footer {
  padding: 4em 0 2em 0;
}
#footer a {
  color: inherit;
  border-bottom-color: rgba(71, 71, 71, 0.25);
}
#footer a:hover {
  color: #75b04b;
  border-bottom-color: transparent;
}
#footer .container {
  margin-bottom: 4em;
}
#footer .icons {
  text-align: center;
  margin: 0;
}
#footer .icons a {
  color: #999;
}
#footer .icons a:hover {
  color: #474747;
}
#footer .copyright {
  color: #999;
  margin-top: 1.5em;
  text-align: center;
  font-size: 0.9em;
}

/* Wide */
@media screen and (max-width: 1680px) {
  /* Basic */
  body,
input,
select,
textarea {
    font-size: 14pt;
    line-height: 1.5em;
  }

  /* Banner */
  #banner {
    height: 24em;
  }
}
/* Normal */
@media screen and (max-width: 1280px) {
  /* Basic */
  body,
input,
select,
textarea {
    font-size: 13pt;
    line-height: 1.5em;
  }

  /* Lists */
  ol.default {
    list-style: decimal;
    padding-left: 1.25em;
  }
  ol.default li {
    padding-left: 0.25em;
  }

  ul.icons li a,
ul.icons li span {
    font-size: 1.5em;
  }

  /* Header */
  #header {
    padding: 2em 0 0 0;
  }
  #header h1, #header .title {
    padding: 0 0 1.75em 0;
  }

  /* Banner */
  #banner {
    height: 20em;
  }

  /* Wrapper */
  .wrapper {
    padding: 3em 0 1em 0;
  }

  /* CTA */
  #cta {
    padding: 2em 0;
  }

  /* Footer */
  #footer {
    padding: 3em 0 3em 0;
  }
  #footer .container {
    margin-bottom: 1em;
  }
}
/* Narrow */
@media screen and (max-width: 980px) {
  /* Basic */
  body,
input,
select,
textarea {
    font-size: 12pt;
    line-height: 1.5em;
  }
}
/* Narrower */
#navPanel,
#titleBar {
  display: none;
}

@media screen and (max-width: 840px) {
  /* Basic */
  html,
body {
    overflow-x: hidden;
  }

  body,
input,
select,
textarea {
    font-size: 13pt;
  }

  h1, #header .title,
h2,
#main_header,
h3,
h4,
h5,
h6 {
    margin-bottom: 0.5em;
  }

  header p {
    margin-top: -0.75em;
  }
  header.major {
    text-align: center;
    margin: 0 0 2em 0;
  }
  header.major h2, header.major #main_header {
    font-size: 1.75em;
  }
  header.major p {
    top: -0.25em;
    font-size: 1.25em;
  }

  /* Box */
  .box.highlight {
    text-align: left;
    position: relative;
    padding-left: 7em;
  }
  .box.highlight i {
    position: absolute;
    margin: 0;
    left: 0;
    top: 0.25em;
  }
  .box.post .inner {
    margin-left: calc(20% + 2em);
  }
  .box.post .image {
    width: 20%;
  }

  /* Header */
  #header {
    display: none;
  }

  /* Banner */
  #banner {
    height: 20em;
  }
  #banner header h2, #banner header #main_header {
    display: block;
  }
  #banner header .button {
    margin: 1em 0 0 0;
  }

  /* CTA */
  #cta {
    padding: 1.5em 0;
  }
  #cta header h2, #cta header #main_header {
    display: block;
  }
  #cta header .button {
    margin: 1em 0 0 0;
  }

  /* Footer */
  #footer {
    text-align: center;
  }
  #footer .container {
    margin-bottom: 4em;
  }

  /* Off-Canvas Navigation */
  #page-wrapper {
    backface-visibility: hidden;
    transition: transform 0.5s ease;
    padding-bottom: 1px;
    padding-top: 44px;
  }

  #titleBar {
    backface-visibility: hidden;
    transition: transform 0.5s ease;
    display: block;
    height: 44px;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10001;
    background-color: #333;
    background-image: linear-gradient(top, rgba(0,0,0,0), rgba(0,0,0,0.3)), url("images/bg01.png");
    height: 44px;
    line-height: 44px;
    box-shadow: 0 4px 0 0 #75b04b;
  }
  #titleBar .title {
    display: block;
    position: relative;
    font-weight: 600;
    text-align: center;
    color: #fff;
    z-index: 1;
  }
  #titleBar .title em {
    font-style: normal;
    font-weight: 300;
  }
  #titleBar .toggle {
    text-decoration: none;
    border: 0;
    height: 60px;
    left: 0;
    position: absolute;
    top: 0;
    width: 80px;
    z-index: 2;
  }
  #titleBar .toggle:before {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-family: FontAwesome;
    font-style: normal;
    font-weight: normal;
    text-transform: none !important;
  }
  #titleBar .toggle:before {
    content: "\f0c9";
    display: block;
    height: 44px;
    text-align: center;
    width: 44px;
    color: #fff;
    opacity: 0.5;
  }
  #titleBar .toggle:active:before {
    opacity: 0.75;
  }

  #navPanel {
    background-color: #1f1f1f;
    box-shadow: inset -1px 0 3px 0 rgba(0, 0, 0, 0.5);
    background-image: linear-gradient(left, rgba(0,0,0,0) 75%, rgba(0,0,0,0.15)), url("images/bg01.png");
    backface-visibility: hidden;
    transform: translateX(-275px);
    transition: transform 0.5s ease;
    display: block;
    height: 100%;
    left: 0;
    overflow-y: auto;
    position: fixed;
    top: 0;
    width: 275px;
    z-index: 10002;
  }
  #navPanel .link {
    border-bottom: 0;
    border-top: solid 1px rgba(255, 255, 255, 0.05);
    color: #888;
    display: block;
    height: 48px;
    line-height: 48px;
    padding: 0 1em 0 1em;
    text-decoration: none;
  }
  #navPanel .link:first-child {
    border-top: 0;
  }
  #navPanel .link.depth-0 {
    color: #fff;
  }
  #navPanel .link .indent-1 {
    display: inline-block;
    width: 1em;
  }
  #navPanel .link .indent-2 {
    display: inline-block;
    width: 2em;
  }
  #navPanel .link .indent-3 {
    display: inline-block;
    width: 3em;
  }
  #navPanel .link .indent-4 {
    display: inline-block;
    width: 4em;
  }
  #navPanel .link .indent-5 {
    display: inline-block;
    width: 5em;
  }

  body.navPanel-visible #page-wrapper {
    transform: translateX(275px);
  }
  body.navPanel-visible #titleBar {
    transform: translateX(275px);
  }
  body.navPanel-visible #navPanel {
    transform: translateX(0);
  }
}
/* Mobile */
@media screen and (max-width: 736px) {
  /* Basic */
  body,
input,
select,
textarea {
    font-size: 11pt;
    line-height: 1.35em;
  }

  h2, #main_header {
    font-size: 1.25em;
    letter-spacing: 0;
    line-height: 1.35em;
  }

  h3 {
    font-size: 1em;
    letter-spacing: 0;
    line-height: 1.35em;
  }

  header p {
    margin-top: -0.5em;
    font-size: 1em;
  }
  header.major {
    padding: 0 20px;
  }
  header.major h2, header.major #main_header {
    font-size: 1.25em;
  }
  header.major p {
    top: 0;
    margin-top: 1.25em;
    font-size: 1em;
  }

  /* Lists */
  ul.menu li {
    border: 0;
    padding: 0;
    margin: 0;
    display: block;
    line-height: 2em;
  }

  /* Banner */
  #banner {
    height: 18em;
  }

  /* Wrapper */
  .wrapper {
    padding: 2em 0 1px 0;
  }
}
/* Mobile (Portrait) */
@media screen and (max-width: 480px) {
  /* Lists */
  ul.actions li {
    display: block;
    padding: 1.5em 0 0 0;
  }
  ul.actions li:first-child {
    padding: 0;
  }

  /* Icon */
  .icon.major {
    width: 4em;
    height: 4em;
    line-height: 4em;
    box-shadow: 0 0 0 7px white, 0 0 0 8px #e0e0e0;
  }
  .icon.major:before {
    font-size: 24px;
  }

  /* Button */
  input[type=submit],
input[type=reset],
input[type=button],
.button {
    width: 100%;
    display: block;
  }

  /* Box */
  .box.highlight {
    padding-left: calc(4em + 30px);
  }
  .box.post .inner {
    margin-left: calc(30% + 20px);
  }
  .box.post .image {
    width: 30%;
  }

  /* Banner */
  #banner {
    height: 20em;
  }
  #banner header {
    padding: 20px;
  }

  /* Wrapper */
  .wrapper {
    padding: 2em 20px 1px 20px;
  }

  /* CTA */
  #cta {
    padding: 20px;
  }

  /* Footer */
  #footer {
    padding: 2em 20px;
    text-align: left;
  }
}
