.message details summary {
  cursor: pointer;
}
.message details .body p {
  color: #fff;
}
.message .close_button {
  width: 1.25em;
  height: 1.25em;
  float: right;
  position: relative;
  overflow: hidden;
  transition: transform 0.5s ease-in-out;
}
.message .close_button:hover {
  transform: rotate(180deg);
}
.message .close_button::before, .message .close_button::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
  background: #fff;
}
.message .close_button::before {
  transform: rotate(45deg);
}
.message .close_button::after {
  transform: rotate(-45deg);
}

.maintenanceitem .main-title {
  font-size: 1.5rem;
  line-height: 2.25rem;
  margin-top: -0.375rem;
}
.maintenanceitem p,
.maintenanceitem h2,
.maintenanceitem #main_header {
  margin-bottom: 0.5em;
}

form input[type=checkbox] {
  vertical-align: top;
}
form label {
  display: inline-block;
  width: calc(100% - 40px);
}
form .errorlist {
  color: red;
}

.icon {
  display: inline-block;
  height: 1em;
  vertical-align: middle;
  margin-top: -0.2em;
}
.icon svg {
  height: 100%;
  display: block;
}

#nav .icon svg {
  path: #fff;
  fill: silver;
  transition: fill 0.2s ease;
}
#nav a:hover svg {
  fill: #fff;
}
#nav .icon-only a {
  padding: 0;
}
#nav .icon-only span {
  display: none;
}
#nav .icon-only + .icon-only {
  margin-left: 0.5em;
}

.maintenance-row {
  display: flex;
  flex-wrap: wrap;
}